import React, { useState, useContext } from "react";
import { Context } from "..";
import { MdDelete } from "react-icons/md";

const Categories: React.FC = () => {
  const [newIncomeValue, setNewIncomeValue] = useState("");
  const [newExpenseValue, setNewExpenseValue] = useState("");
  const { expenseCategories, incomeCategories } = useContext(Context);

  const incomeFormValid =
    newIncomeValue.length > 0 &&
    !incomeCategories[0]
      .map((it) => it.id)
      .includes(`INCOME_${newIncomeValue.replace(/ /g, "_").toUpperCase()}`);

  const expenseFormValid =
    newExpenseValue.length > 0 &&
    !expenseCategories[0]
      .map((et) => et.id)
      .includes(`EXPENSE_${newExpenseValue.replace(/ /g, "_").toUpperCase()}`);

  const addIncomeCategory = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const newCat = {
      id: `INCOME_${newIncomeValue.replace(/ /g, "_").toUpperCase()}`,
      label: newIncomeValue,
    };
    incomeCategories[1]((cigs) => [newCat, ...cigs]);
    setNewIncomeValue("");
  };

  const addExpenseCategory = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const newCat = {
      id: `EXPENSE_${newExpenseValue.replace(/ /g, "_").toUpperCase()}`,
      label: newExpenseValue,
    };
    expenseCategories[1]((cegs) => [newCat, ...cegs]);
    setNewExpenseValue("");
  };

  const deleteCategory = (id: string) => {
    if (id.startsWith("INCOME")) {
      incomeCategories[1]((cigs) => {
        return cigs.filter((cig) => cig.id !== id);
      });
    } else if (id.startsWith("EXPENSE")) {
      expenseCategories[1]((cegs) => {
        return cegs.filter((ceg) => ceg.id !== id);
      });
    } else {
      console.warn("unknown id of category");
    }
  };

  return (
    <>
      <h1 className="text-3xl mt-10 mb-4">Categories</h1>
      <div className="p-5 bg-gray-50 rounded-md shadow-md">
        <form onSubmit={addIncomeCategory}>
          <div className="flex mb-2 mt-5">
            {incomeCategories[0]
              .filter((ec) => ec.default === undefined || ec.default === false)
              .map((cig) => (
                <div
                  className="flex items-center px-4 py-1 rounded-full bg-gray-300 mr-2"
                  key={cig.id}
                >
                  <span>{cig.label}</span>
                  <MdDelete
                    className="ml-6 cursor-pointer text-red-500"
                    onClick={() => deleteCategory(cig.id)}
                  />
                </div>
              ))}
          </div>
          <div className="flex sm:items-center flex-col sm:flex-row">
            <div className="w-full sm:w-auto">Add Income Category:</div>
            <input
              value={newIncomeValue}
              onChange={(e) => setNewIncomeValue(e.target.value)}
              type="text"
              placeholder="Income category"
              className="px-4 py-1 rounded-md border border-gray-400 my-2 sm:my-0 sm:ml-4"
            />
            <button
              disabled={!incomeFormValid}
              className={`rounded-md sm:ml-4 text-white px-24 py-2 w-full sm:w-auto ${
                incomeFormValid
                  ? "bg-green-600"
                  : "bg-gray-500 cursor-not-allowed"
              }`}
              type="submit"
            >
              Add
            </button>
          </div>
        </form>
        <form onSubmit={addExpenseCategory}>
          <div className="flex mb-2 mt-5">
            {expenseCategories[0]
              .filter((ec) => ec.default === undefined || ec.default === false)
              .map((ceg) => (
                <div
                  className="flex items-center px-4 py-1 rounded-full bg-gray-300 mr-2"
                  key={ceg.id}
                >
                  <span>{ceg.label}</span>
                  <MdDelete
                    className="ml-6 cursor-pointer text-red-500"
                    onClick={() => deleteCategory(ceg.id)}
                  />
                </div>
              ))}
          </div>
          <div className="flex sm:items-center flex-col sm:flex-row">
            <div className="w-full sm:w-auto">Add Expense Category:</div>
            <input
              value={newExpenseValue}
              onChange={(e) => setNewExpenseValue(e.target.value)}
              placeholder="Expense category"
              type="text"
              className="px-4 py-1 rounded-md border border-gray-400 my-2 sm:my-0 sm:ml-4"
            />
            <button
              disabled={!expenseFormValid}
              className={`rounded-md sm:ml-4 text-white px-24 py-2 w-full sm:w-auto ${
                expenseFormValid
                  ? "bg-green-600"
                  : "bg-gray-500 cursor-not-allowed"
              }`}
              type="submit"
            >
              Add
            </button>
          </div>
        </form>
      </div>
    </>
  );
};

export default Categories;
